*{
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}
.contt__control{
  margin-right: 20px;
  cursor: pointer;
  font-size: 14px;
}
.contt__control input[type="radio"]{
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.contt__control input[type="radio"] ~ span{
  padding: 4px 7px;
  letter-spacing: -0.6px;
  font-size: 12px;
  display: inline-block;
  background-color: #eee;
  color: #222;
}
.contt__control input[type="radio"]:checked ~ span{
  background-color: #f68000;
  color: #fff;
}

.contt__control-perrow{
  margin-right: 0;
  position: absolute;
  right: 0;
  bottom: 0;
}
.contt__control-perrow select{
  width: 40px;
  padding: 0 5px;
  background: transparent;
  border-bottom: none;
}

button.contt__control{
  background-color: transparent;
  border: none;
  padding: 0;
  border-bottom: 1px dashed #666;
  color: #666;
}

select{
  background-color: #eee;
  color: #222;
  height: 38px;
  padding: 0px 20px 0px 10px;
  width: 150px;
  border: none;
  border-bottom: 1px solid #ccc;
  font-size: 14px;
}

select.contt__control:focus{
  background-color: #eee;
}

.contt{
  /* max-width: 1200px; */
  margin: 0px	0 20px;
}
.contt__filterz{
  margin-bottom: 10px;
  border-bottom: 1px solid #ccc;
  padding: 15px 60px 10px 0;
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  position: relative;
}
.gridd{
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  flex-direction: row;
}
.gridd__wrr{
  padding: 15px 10px;
  width: 100%;
}
.gridd__item{
  border-radius: 6px;
  display: flex;
  height: 100%;
  flex-direction: column;
  box-shadow: 0 0 10px #999;
}
.gridd__head{
  background-color: #33cccc;
  color: #fff;
  position: relative;
  border-radius: 6px 6px 0 0;
  padding: 10px 15px;
  display: flex;
  align-items: flex-end;
}
.gridd__head-logo{
  margin-right: 15px;
  align-self: center;
  max-height: 80px;
  max-width: 80px;
}
.gridd__head-until{
  font-size: 13px;
  margin: 0;
}
.gridd__head-discount{
  position: absolute;
  width: 80px;
  height: 75px;
  background: url('./imgz/plash.png') no-repeat center;
  top: -10px;
  right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 15px;
  padding-left: 13px;
  font-weight: 700;
}
.gridd__body{
  padding: 20px 15px;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #fff;
  position: relative;
}
.gridd__body h3{
  margin: 0 0 15px;
  color: #428bca;
  font-size: 18px;
  font-weight: bold;
}
.gridd__butts{
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  width: 100%;
  z-index: 2;
  /* flex-wrap: wrap; */
}
.gridd__butt{
  padding: 10px 0px;
  font-size: 11px;
  white-space: nowrap;
  text-decoration: none;
  font-weight: bold;
  box-shadow: 0 0 4px #666;
  width: 100%;
  max-width: 160px;
}
.gridd__butt.gridd__butt-url{
  background-color: #33cc99;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: -0.6px;
  text-shadow: 0 0 2px #000;
}
.gridd__butt.gridd__butt-url:hover,
.gridd__butt.gridd__butt-url:visited{
  background-color: rgba(51,204,153,0.72);
  color: #fff;
}
.gridd__butts i{
  margin-left: 5px;
}
.gridd__butt-code{
  border: 1px solid #33cc99;
  color: #33cc99;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 5px;
  padding-right: 5px;
}
.gridd__butt-code span{
  display: inline-block;
  width: 85%;
  overflow: hidden;
  font-weight: bold;
}

.gridd__body-txt{
  margin: 0 0 15px;
}
.gridd__body-txt p{
  margin: 0;
}



.gridd__item[data-type*="blackfriday"] .gridd__body,
.gridd__item[data-type*="x-mas"] .gridd__body{
  color: rgb(255, 255, 255);
}
.gridd__item[data-type*="blackfriday"] .gridd__body:before,
.gridd__item[data-type*="x-mas"] .gridd__body:before{
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.5;
}
.gridd__item[data-type*="blackfriday"] .gridd__body h3,
.gridd__item[data-type*="blackfriday"] .gridd__body p,
.gridd__item[data-type*="x-mas"] .gridd__body h3,
.gridd__item[data-type*="x-mas"] .gridd__body p{
  color: #fff;
  z-index: 2;
  position: relative;
  text-shadow: 0px 0 3px #fff;
}



.gridd__item[data-type*="blackfriday"] .gridd__body{
  background: url('https://vpntester.org/wp-content/uploads/2021/11/76580-Black-friday-background.png') center no-repeat;
  background-size: cover;
}

.gridd__item[data-type*="x-mas"] .gridd__body{
  background: url('https://vpntester.org/wp-content/uploads/2021/12/christmas-g9a1f9f9cb_640.jpg') center no-repeat;
  background-size: cover;
}



/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (max-width: 992px) {
  .gridd__wrr{
    max-width: 50% !important;
  }
  .contt__control-perrow{
    position: static;
  }
  .contt__filterz{
    justify-content: space-between;
  }
  .contt__filterz{
    padding-right: 0;
  }
}

@media only screen and (max-width: 600px) {
  .gridd__wrr{
    max-width: 100% !important;
  }
  .contt__control{
    width: 100%;
    margin-right: 0;
    margin-bottom: 10px;
  }
  .gridd__butt{
    max-width: 100%;
    width: 100%;
    margin-top: 10px;
  }
}